import { Component } from '@angular/core';

@Component({
  selector: 'app-cars',
  templateUrl: './cars.component.html',
  styleUrls: ['./cars.component.css']
})
export class CarsComponent {
  addCarStatus = false;
  inputText = 'Default Text';
  carName = '';
  cars = ['Ford', 'Mazda', 'BMW', 'Ferrary', 'Audi', 'Tesla'];
  itemsTest = [{id: 3, name: 'item 1'}, {id: 6, name: 'item 2'}, {id: 9, name: 'item 3'}];
  dates = [
    new Date(2015, 3, 4).toDateString(),
    new Date(2011, 5, 6).toDateString(),
    new Date(2016, 1, 7).toDateString(),
    new Date(2010, 4, 11).toDateString()
  ]

  constructor() {
  }

  addCar() {
    this.addCarStatus = true;
    this.cars.push(this.carName);
    this.carName = '';
  }

  onKeyUp (event) {
    this.inputText = event.target.value;
  }

  setBigCarText(car: string) {
    return car.length > 4 ? true : false;
  }
}
