import {Component, ViewEncapsulation} from '@angular/core';

@Component({
  selector: 'app-cars-b3',
  templateUrl: './cars-b3.component.html',
  styleUrls: ['./cars-b3.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class CarsB3Component {

  cars: [{name: string, year: number}/*, {name: string, year: number}, {name: string, year: number}*/] = [
    {
      name: 'Ford',
      year: 2015
    },
    // {
    //   name: 'Mazda',
    //   year: 2010
    // },
    // {
    //   name: 'Audi',
    //   year: 2017
    // }
  ];

  constructor() { }

  updateCarList(car: {name: string, year: number}) {
    this.cars.push(car);
  }

  changeCarName() {
    this.cars[0].name = 'New car Name!';
  }

  deleteCar() {
    this.cars.splice(0, 1);
  }

}
